import {
  Box,
  Card,
  CardMedia,
  CssBaseline,
  Divider,
  Grid,
  Stack,
  Typography,
  useTheme,
} from "@mui/material";
import { MainFooter } from "components/Footer";
import constants from "theme/constants";
import contentImage from "assets/intro_placeholder.png";
import React, { useEffect } from "react";
import Aos from "aos";
import "aos/dist/aos.css";
import { getDatabase, ref, child, get } from "firebase/database";
import Loading from "components/Loading";

const getData = async () => {
  try {
    const dbRef = ref(getDatabase());
    const snapshot = await get(child(dbRef, `intro`));
    if (snapshot.exists()) {
      return snapshot.val();
    } else {
      console.log("No data available");
    }
  } catch (error) {
    console.log(error);
  }
  return null;
};

const parseUserName = (subtitle, theme, ANIMATION_STYLE) => {
  if (!subtitle) {
    return <></>;
  }
  var userName = "";
  var str = "";
  const parsed = subtitle.split(" ");

  console.log(subtitle);
  parsed.forEach((item, index) => {
    if (item === "Ali") {
      userName += item;
    } else if (item === "Ahnaf") {
      userName += ` ${item}`;
    } else {
      str += ` ${item}`;
    }
  });

  return (
    <Typography variant="h3" data-aos={ANIMATION_STYLE}>
      {str}{" "}
      <span style={{ color: theme.palette.main.cuteBlue }}>{userName}</span>
    </Typography>
  );
};

const IntroPage = () => {
  const theme = useTheme();
  const { ANIMATION_STYLE } = constants;
  const [data, setData] = React.useState(null);
  const [loading, setLoading] = React.useState(true);

  useEffect(() => {
    Aos.init({ duration: 1000 });
    getData()
      .then((data) => {
        setData(data);
        console.log("DATA FETCHED", data);

        setLoading(false);
      })
      .catch((error) => {
        console.log("ERROR");
      });
  }, []);

  if (data === null || loading) return <Loading />;

  return (
    <Box
      sx={{
        display: "flex",
        flexDirection: "column",
        minHeight: "100vh",
      }}
    >
      <CssBaseline />
      {/* content */}
      <Box
        sx={{
          flexGrow: 1,
          marginLeft: constants.CONTENT_SPACING,
          marginRight: constants.CONTENT_SPACING,
          marginTop: "5%",
        }}
      >
        <Grid container spacing={3}>
          {/* left side content */}
          <Grid item xs={12} md={12} sm={12} lg={6}>
            <Stack spacing={1}>
              <Typography variant="h1" data-aos={ANIMATION_STYLE}>
                {data && data.title}
              </Typography>
              {parseUserName(data && data.subtitle, theme, ANIMATION_STYLE)}

              <Divider
                data-aos={ANIMATION_STYLE}
                style={{
                  marginTop: theme.spacing(2),
                  marginBottom: theme.spacing(2),
                }}
              />
              <Typography
                variant="body1"
                sx={{ color: theme.palette.main.ashLight }}
                data-aos={ANIMATION_STYLE}
              >
                {data && data.content}
              </Typography>
            </Stack>
          </Grid>
          {/* right side content */}
          <Grid
            item
            xs
            sx={{
              display: "flex",
              flexDirection: "column",
              alignContent: "flex-end",
              justifyContent: "space-around",
              flexWrap: "wrap",
              [theme.breakpoints.down("lg")]: {
                alignContent: "space-around",
              },
            }}
          >
            <Card
              sx={{
                maxWidth: 550,
                boxShadow: "none",
                borderRadius: 0,
                [theme.breakpoints.down("lg")]: {
                  maxWidth: 400,
                },
              }}
              data-aos={ANIMATION_STYLE}
            >
              <CardMedia
                sx={{ height: "100%" }}
                image={data && data.image ? data.image : contentImage}
                component="img"
                alt="user image"
              />
            </Card>
          </Grid>
        </Grid>
      </Box>

      <Box
        sx={{ overflow: "auto", paddingTop: 20 }}
        flexDirection={"column"}
        flexGrow={1}
      />
      {/* footer */}
      <MainFooter />
    </Box>
  );
};

export default IntroPage;
