import styled from "@emotion/styled";
import {
  Box,
  Button,
  CssBaseline,
  Divider,
  Grid,
  Snackbar,
  Stack,
  TextField,
  Typography,
  useTheme,
} from "@mui/material";
import { MainFooter } from "components/Footer";
import constants from "theme/constants";
import Aos from "aos";
import "aos/dist/aos.css";
import React, { useEffect } from "react";
import { getDatabase, ref, push, set } from "firebase/database";
import Lottie from "react-lottie";
import animationData from "assets/lotties/mail_sent.json";

// create a styled text field
const StyledTextField = styled(TextField)(({ theme }) => ({
  borderBottomColor: theme.palette.main.cuteBlue,
  // border: `1px solid red`,
  // "&:hover": {
  //   borderBottom: `0.2px solid red`,
  // },
}));

const writeData = async (data) => {
  const db = getDatabase();
  const mailRef = ref(db, "mailingList");
  const autoKeyRef = push(mailRef);
  await set(autoKeyRef, data);
};

const HEADERS = [
  "GET IN TOUCH",
  "Enthusiast with an immense interest in We , UI/UX Design, and Cloud Computing.",
  "Name",
  "Email",
  "Message",
  "Send",
];

const SNACKBAR_MESSAGE = {
  success: "Thank you for your interest! I will get back to you soon! ",
  error: "Please enter the information correctly! ",
};

const getClientLocation = async () => {
  const response = await fetch("https://geolocation-db.com/json/");
  const data = await response.json();
  return data;
};

const isFormValid = (form) => {
  return (
    form.name &&
    form.name.length > 0 &&
    form.email &&
    form.email.length > 0 &&
    form.message &&
    form.message.length > 0 &&
    /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(form.email)
  );
};

const MailSent = ({ setSnackbarConfig }) => {
  const theme = useTheme();

  const defaultOptions = {
    loop: false,
    autoplay: true,
    animationData: animationData,
    rendererSettings: {
      preserveAspectRatio: "xMidYMid slice",
    },
  };

  const onAnimationComplete = () => {
    console.log("animation completed");
    setSnackbarConfig((prev) => ({
      ...prev,
      status: false,
    }));
  };

  return (
    <div
      style={{ marginTop: theme.spacing(5), position: "absolute", zIndex: 10 }}
    >
      <Lottie
        options={defaultOptions}
        style={{
          height: "45%",
          width: "45%",
        }}
        eventListeners={[
          {
            eventName: "complete",
            callback: onAnimationComplete,
          },
        ]}
      />
    </div>
  );
};

const ContactPage = () => {
  const theme = useTheme();
  const { ANIMATION_STYLE } = constants;
  const [input, setInput] = React.useState({});
  const [snackbarConfig, setSnackbarConfig] = React.useState({});
  const [error, setError] = React.useState(false);

  useEffect(() => {
    Aos.init({ duration: 1000 });
  }, []);

  const handleChange = (event) => {
    const { name, value } = event.target;
    setInput((prevState) => ({
      ...prevState,
      [name]: value,
    }));
  };

  const onSendClick = (e) => {
    e.preventDefault();
    console.log(input);

    if (isFormValid(input)) {
      getClientLocation().then((data) => {
        const { city, country_name, IPv4 } = data;
        input.city = city;
        input.country = country_name;
        input.IPv4 = IPv4;

        writeData(input);

        setError(false);
        setSnackbarConfig({
          status: true,
          message: SNACKBAR_MESSAGE.success,
        });
      });
    } else {
      setError(true);
      setSnackbarConfig({
        status: true,
        message: SNACKBAR_MESSAGE.error,
      });
    }
  };

  return (
    <Box
      sx={{
        display: "flex",
        flexDirection: "column",
        minHeight: "100vh",
      }}
    >
      {snackbarConfig.status && !error && (
        <MailSent setSnackbarConfig={setSnackbarConfig} />
      )}

      <Snackbar
        anchorOrigin={{
          vertical: "bottom",
          horizontal: "center",
        }}
        open={snackbarConfig.status}
        autoHideDuration={5000}
        message={snackbarConfig.message}
        onClose={() => setSnackbarConfig({ ...snackbarConfig, status: false })}
      />
      <CssBaseline />
      {/* content */}
      <Box
        sx={{
          flexGrow: 1,
          marginLeft: constants.CONTENT_SPACING,
          marginRight: constants.CONTENT_SPACING,
          marginTop: "5%",
        }}
      >
        <Stack spacing={1}>
          <Typography variant="h2" data-aos={ANIMATION_STYLE}>
            {HEADERS[0]}
          </Typography>
          <Divider
            data-aos={ANIMATION_STYLE}
            style={{
              marginTop: theme.spacing(2),
              marginBottom: theme.spacing(2),
            }}
          />
          <Typography data-aos={ANIMATION_STYLE} variant="body1">
            {HEADERS[1]}
          </Typography>
          <Grid
            component="form"
            width="100%"
            sx={{
              [theme.breakpoints.up("sm")]: {
                width: theme.spacing(50),
              },
            }}
            noValidate
            autoComplete="off"
          >
            <StyledTextField
              required
              id="outlined-required"
              name="name"
              label={HEADERS[2]}
              type="text"
              fullWidth
              variant="filled"
              onChange={handleChange}
              sx={{ marginTop: theme.spacing(4) }}
              data-aos={ANIMATION_STYLE}
            />
            <StyledTextField
              required
              id="outlined-required"
              label={HEADERS[3]}
              variant="filled"
              fullWidth
              name="email"
              onChange={handleChange}
              type="email"
              sx={{ marginTop: theme.spacing(4) }}
              data-aos={ANIMATION_STYLE}
            />

            <StyledTextField
              required
              id="outlined-required"
              label={HEADERS[4]}
              variant="filled"
              type="text"
              fullWidth
              name="message"
              onChange={handleChange}
              multiline
              sx={{ marginTop: theme.spacing(4) }}
              data-aos={ANIMATION_STYLE}
            />

            <Button
              variant="contained"
              color="primary"
              fullWidth
              data-aos={ANIMATION_STYLE}
              onClick={onSendClick}
              sx={{
                borderRadius: 0,
                padding: theme.spacing(2),
                marginTop: theme.spacing(4),
                backgroundColor: theme.palette.main.cuteBlue,
                boxShadow: "none",
                transition: "all 0.2s ease-in-out",
                [theme.breakpoints.up("sm")]: {
                  "&:hover": {
                    boxShadow: "none",
                    backgroundColor: theme.palette.main.cuteBlueShade,
                    // add hover transition
                    transition: "all 0.2s ease-in-out",
                  },
                },
                [theme.breakpoints.down("sm")]: {
                  "&:hover": {
                    boxShadow: "none",
                    backgroundColor: theme.palette.main.cuteBlue,
                    // add hover transition
                  },
                },
              }}
            >
              {HEADERS[5]}
            </Button>
          </Grid>
        </Stack>
      </Box>

      <Box
        sx={{ overflow: "auto", paddingTop: 20 }}
        flexDirection={"column"}
        flexGrow={1}
      />
      {/* footer */}
      <MainFooter />
    </Box>
  );
};

export default ContactPage;
