import PropTypes from "prop-types";
// material
import { styled } from "@mui/material/styles";
import { Box, Stack, Toolbar, IconButton, Typography } from "@mui/material";
// components
import { MHidden } from "components/@material-extend";
import MuiAppBar from "@mui/material/AppBar";
import React from "react";
import { useTheme } from "@mui/material/styles";
import useWindowDimensions from "util/windowSize";
import breakpoints from "theme/breakpoints";
import HamburgerMenu from "./Hamburger/HamburgerMenu";
import FacebookIcon from "@mui/icons-material/Facebook";
import GitHubIcon from "@mui/icons-material/GitHub";
import LinkedInIcon from "@mui/icons-material/LinkedIn";
import MenuIcon from "@mui/icons-material/Menu";
import StyledLink from "components/StyledLink";

const DRAWER_WIDTH = 100;
const APPBAR_MOBILE = 64;

const ToolbarStyle = styled(Toolbar)(({ theme }) => ({
  minHeight: APPBAR_MOBILE,
  paddingTop: theme.spacing(1),
}));

const AppBarStyle = styled(MuiAppBar, {
  shouldForwardProp: (prop) => prop !== "open",
})(({ theme }) => ({
  marginLeft: DRAWER_WIDTH,
  boxShadow: "none",
  backgroundColor: theme.palette.main.background,
  [theme.breakpoints.up("md")]: {
    width: `calc(100% - ${DRAWER_WIDTH}px)`,
  },
}));

// ----------------------------------------------------------------------

export default function Navbar() {
  const [isMenuOpen, setMenuOpen] = React.useState(false);
  const { width } = useWindowDimensions();
  const theme = useTheme();

  var social = localStorage.getItem("socialMedia");
  if (social) {
    social = JSON.parse(social);
    console.log(social);
  }

  const onHamburgerClick = () => {
    setMenuOpen(!isMenuOpen);
  };

  if (isMenuOpen && width <= breakpoints.values.md) {
    return <HamburgerMenu setMenuOpen={setMenuOpen} isMenuOpen={isMenuOpen} />;
  }

  return (
    <Box sx={{ display: "flex" }}>
      <AppBarStyle position="absolute">
        <ToolbarStyle>
          <Typography
            variant="body1"
            color="inherit"
            noWrap
            sx={{
              flexGrow: 1,
              [theme.breakpoints.up("md")]: {
                marginLeft: theme.spacing(3),
              },
              color: theme.palette.main.black,
              fontFamily: theme.typography.fontTitleFont,
              cursor: "pointer",
            }}
          >
            <StyledLink
              to={"/"}
              sx={{
                textDecoration: "none",
                color: "inherit",
                transition: "all 0.2s ease",
                // add hover effect for all except mobile
                WebkitUserSelect: "none",
                MozUserSelect: "none",
                msUserSelect: "none",
                userSelect: "none",
                [theme.breakpoints.up("sm")]: {
                  "&:hover": {
                    color: theme.palette.main.cuteBlue,
                    transition: "all 0.2s ease",
                  },
                },
              }}
            >
              Ahnaf Swapnil
            </StyledLink>
          </Typography>
          {/* normal view */}
          <MHidden width="mdDown">
            <Stack
              direction="row"
              alignItems="center"
              spacing={{ xs: 0.5, sm: 1.5 }}
            >
              <IconButton
                size="large"
                color={"primary"}
                target="_blank"
                href={social && social.facebook}
              >
                <FacebookIcon color="secondary" />
              </IconButton>

              <IconButton
                size="large"
                target="_blank"
                href={social && social.github}
              >
                <GitHubIcon
                  color="primary"
                  sx={{ color: theme.palette.main.black }}
                />
              </IconButton>

              <IconButton
                size="large"
                target="_blank"
                href={social && social.linkedin}
              >
                <LinkedInIcon
                  color="secondary"
                  sx={{ color: theme.palette.main.blue }}
                />
              </IconButton>
            </Stack>
          </MHidden>
          {/* hamburger icon when device in mobile view */}
          <MHidden width="mdUp">
            <IconButton size="large" onClick={onHamburgerClick}>
              <MenuIcon
                color="primary"
                sx={{ color: theme.palette.main.black }}
              />
            </IconButton>
          </MHidden>
        </ToolbarStyle>
      </AppBarStyle>
    </Box>
  );
}

HamburgerMenu.propTypes = {
  setMenuOpen: PropTypes.func,
};

Navbar.propTypes = {
  onOpenSidebar: PropTypes.func,
};
