import PropTypes from "prop-types";
// material
import { Box } from "@mui/material";
import image from "assets/brand_logo.svg";

// ----------------------------------------------------------------------

Logo.propTypes = {
  sx: PropTypes.object,
};

export default function Logo({ sx }) {
  return (
    <Box
      component="img"
      src={image}
      sx={{ width: 40, height: 40, ...sx }}
    />
  );
}
