import * as React from "react";
import Tabs from "@mui/material/Tabs";
import Tab from "@mui/material/Tab";
import Typography from "@mui/material/Typography";
import Box from "@mui/material/Box";
import styled from "@emotion/styled";
import { Button, Stack } from "@mui/material";
import NavigateNextIcon from "@mui/icons-material/NavigateNext";
import NavigateBeforeIcon from "@mui/icons-material/NavigateBefore";
import { useTheme } from "@emotion/react";
import constants from "theme/constants";

function TabPanelEducation(props) {
  const { children, value, index, ...other } = props;

  const theme = useTheme();
  console.log(children);

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`vertical-tabpanel-${index}`}
      aria-labelledby={`vertical-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Stack sx={{ pt: (theme) => theme.spacing(2) }} spacing={1}>
          <Typography variant="subtitle1">{children.institution} </Typography>
          <Typography variant="body2" sx={{ color: theme.palette.main.purple }}>
            {children.program}
          </Typography>
          <Typography variant="body2">CGPA {children.cgpa}</Typography>
          <Typography variant="body2">
            {children.startDate} to {children.startDate}
          </Typography>
        </Stack>
      )}
    </div>
  );
}

function TabPanelWork({ children, value, index, ...other }) {
  const theme = useTheme();
  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`vertical-tabpanel-${index}`}
      aria-labelledby={`vertical-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Stack sx={{ pt: (theme) => theme.spacing(2) }} spacing={1}>
          <Typography variant="subtitle1">{children.company} </Typography>
          <Typography variant="body2" sx={{ color: theme.palette.main.purple }}>
            {children.title}
          </Typography>
          <Typography variant="body2">
            {children.startDate} to {children.startDate}
          </Typography>
        </Stack>
      )}
    </div>
  );
}

const Education = ({ content }) => {
  const [value, setValue] = React.useState(0);
  const { ANIMATION_STYLE } = constants;

  console.log(content);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  return (
    <Box
      sx={{ maxWidth: 500, bgcolor: (theme) => theme.palette.main.background }}
      data-aos={ANIMATION_STYLE}
    >
      <Tabs
        value={value}
        onChange={handleChange}
        scrollButtons
        aria-label="scrollable force tabs example"
        sx={{ borderBottom: 1, borderColor: "divider" }}
      >
        {content.map((item, index) => {
          const { type } = item;
          return <StyledTab label={type} {...a11yProps(index)} key={index} />;
        })}
      </Tabs>
      {content.map((item, index) => {
        return (
          <TabPanelEducation
            value={value}
            index={index}
            key={index}
            data-aos={ANIMATION_STYLE}
          >
            {item}
          </TabPanelEducation>
        );
      })}

      <Navigation content={content} value={value} setValue={setValue} />
    </Box>
  );
};

const Navigation = ({ value, setValue, content }) => {
  const { ANIMATION_STYLE } = constants;
  return (
    <Stack
      flexDirection={"row"}
      sx={{ marginTop: (theme) => theme.spacing(3) }}
      data-aos={ANIMATION_STYLE}
    >
      {/* next button */}
      <Button
        startIcon={<NavigateBeforeIcon />}
        data-aos={ANIMATION_STYLE}
        sx={{
          padding: 1,
          backgroundColor: (theme) => theme.palette.main.lightGreen,
          // add hover effect
          "&:hover": {
            backgroundColor: (theme) => theme.palette.main.lightGreenShade,
          },
        }}
        onClick={(e) => {
          e.preventDefault();
          value > 0 && setValue(value - 1);
        }}
      >
        Prev
      </Button>
      <Box sx={{ flexGrow: 1 }} />

      {/* previous button */}
      <Button
        endIcon={<NavigateNextIcon />}
        data-aos={ANIMATION_STYLE}
        sx={{
          padding: 1,
          backgroundColor: (theme) => theme.palette.main.lightGreen,
          // add hover effect
          "&:hover": {
            backgroundColor: (theme) => theme.palette.main.lightGreenShade,
          },
        }}
        onClick={(e) => {
          e.preventDefault();
          value < content.length - 1 && setValue(value + 1);
        }}
      >
        Next
      </Button>
    </Stack>
  );
};

const WorkExperience = ({ content }) => {
  const [value, setValue] = React.useState(0);
  const { ANIMATION_STYLE } = constants;

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  return (
    <Box
      sx={{ maxWidth: 500, bgcolor: (theme) => theme.palette.main.background }}
      data-aos={ANIMATION_STYLE}
    >
      <Tabs
        value={value}
        onChange={handleChange}
        scrollButtons
        aria-label="scrollable force tabs example"
        sx={{ borderBottom: 1, borderColor: "divider" }}
      >
        {content.map((item, index) => {
          const { title } = item;
          return <StyledTab label={title} {...a11yProps(index)} key={index} />;
        })}
      </Tabs>
      {content.map((item, index) => {
        return (
          <TabPanelWork value={value} index={index} key={index}>
            {item}
          </TabPanelWork>
        );
      })}

      <Navigation content={content} value={value} setValue={setValue} />
    </Box>
  );
};

export default function TabLayoutMobile({ content, type, ...props }) {
  if (type === "education") {
    return <Education content={content} />;
  } else {
    return <WorkExperience content={content} />;
  }
}

function a11yProps(index) {
  return {
    id: `vertical-tab-${index}`,
    "aria-controls": `vertical-tabpanel-${index}`,
  };
}

// create a styled Tab component
const StyledTab = styled(Tab)(({ theme }) => ({
  ...theme.typography.subtitle1,
  WebkitAlignItems: "left",
  paddingLeft: theme.spacing(0),
  paddingTop: theme.spacing(1),
  paddingBottom: theme.spacing(1),
  paddingRight: theme.spacing(2),
  alignItems: "flex-start",
  textAlign: "left",
}));
