import * as React from "react";
import Dialog from "@mui/material/Dialog";
import Toolbar from "@mui/material/Toolbar";
import IconButton from "@mui/material/IconButton";
import CloseIcon from "@mui/icons-material/Close";
import Slide from "@mui/material/Slide";
import { useTheme } from "@mui/material/styles";
import { Box } from "@mui/material";
import { styled } from "@mui/material/styles";
import MuiAppBar from "@mui/material/AppBar";
import { MHidden } from "components/@material-extend";
import MenuIcon from "@mui/icons-material/Menu";
import NavSectionMobile from "./NavSectionMobile";
import { MobileFooter } from "components/Footer";
import sidebarConfig from "../config/SidebarConfig";

const DRAWER_WIDTH = 100;
const APPBAR_MOBILE = 64;
const APPBAR_DESKTOP = 92;

const ToolbarStyle = styled(Toolbar)(({ theme }) => ({
  minHeight: APPBAR_MOBILE,
  [theme.breakpoints.up("lg")]: {
    minHeight: APPBAR_DESKTOP,
    padding: theme.spacing(0, 5),
  },
}));

const AppBarStyle = styled(MuiAppBar, {
  shouldForwardProp: (prop) => prop !== "open",
})(({ theme }) => ({
  marginLeft: DRAWER_WIDTH,
  boxShadow: "none",
  display: "contents",
  backgroundColor: theme.palette.main.black,
  [theme.breakpoints.up("md")]: {
    width: `calc(100% - ${DRAWER_WIDTH}px)`,
  },
}));

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

export default function HamburgerMenu({ setMenuOpen, isMenuOpen, ...other }) {
  const theme = useTheme();

  const handleClose = () => {
    setMenuOpen(false);
  };

  return (
    <div>
      <Dialog
        fullScreen
        open={isMenuOpen}
        onClose={handleClose}
        TransitionComponent={Transition}
        sx={{
          justifyContent: "center",
          textAlign: "center",
        }}
      >
        <AppBarStyle
          position="absolute"
          sx={{ backgroundColor: theme.palette.main.ashLight2 }}
        >
          <ToolbarStyle>
            <Box flexGrow={1} />
            <MHidden width="mdUp">
              <IconButton
                size="large"
                color={"primary"}
                onClick={(e) => {
                  e.preventDefault();
                  setMenuOpen(false);
                }}
              >
                {!isMenuOpen && (
                  <MenuIcon
                    color="primary"
                    sx={{ color: theme.palette.main.black }}
                  />
                )}

                {isMenuOpen && (
                  <CloseIcon
                    color="primary"
                    sx={{ color: theme.palette.main.black }}
                  />
                )}
              </IconButton>
            </MHidden>
          </ToolbarStyle>
        </AppBarStyle>
        {/* body */}
        <NavSectionMobile navConfig={sidebarConfig} setMenuOpen={setMenuOpen} />
        {/* footer */}
        <MobileFooter />
      </Dialog>
    </div>
  );
}
