import {
  Avatar,
  Box,
  CssBaseline,
  Divider,
  Grid,
  Stack,
  Typography,
  useTheme,
} from "@mui/material";
import { MainFooter } from "components/Footer";
import { styled } from "@mui/material/styles";
import constants from "theme/constants";
import React from "react";
import "aos/dist/aos.css";
import Aos from "aos";
import { child, get, getDatabase, ref } from "firebase/database";
import Loading from "components/Loading";

const StyledAvatar = styled(Avatar)(({ theme }) => ({
  width: constants.SKILL_AVATAR_SIZE,
  height: constants.SKILL_AVATAR_SIZE,
  "@media (max-width: 600px)": {
    width: constants.SKILL_AVATAR_SIZE_SMALL_SCREEN,
    height: constants.SKILL_AVATAR_SIZE_SMALL_SCREEN,
  },
  marginLeft: theme.spacing(0),
  marginRight: theme.spacing(1),
  marginTop: theme.spacing(1),
  "&:not(:first-of-child)": {
    marginLeft: theme.spacing(0),
  },
  "&(:first-of-child)": {
    marginLeft: theme.spacing(0),
  },

  transition: "all 0.5s ease-in-out",
  "&:hover": {
    transform: "scale(1.1)",
    transition: "all 0.2s ease-in-out",
  },
}));

const getData = async () => {
  try {
    const dbRef = ref(getDatabase());
    const snapshot = await get(child(dbRef, `skills`));
    if (snapshot.exists()) {
      return snapshot.val();
    } else {
      console.log("No data available");
    }
  } catch (error) {
    console.log(error);
  }
  return null;
};

const HEADERS = ["SKILLS", "Expertise", "Comfortable", "Tools"];

const SkillsPage = () => {
  const theme = useTheme();
  const { ANIMATION_STYLE } = constants;
  const [data, setData] = React.useState(null);
  const [loading, setLoading] = React.useState(true);

  React.useEffect(() => {
    Aos.init({ duration: 1000 });
    getData()
      .then((data) => {
        setData(data);
        console.log(data);
        setLoading(false);
      })
      .catch((error) => {
        console.log(error);
      });
  }, []);

  if (data === null || loading) return <Loading />;

  return (
    <Box
      sx={{
        display: "flex",
        flexDirection: "column",
        minHeight: "100vh",
      }}
    >
      <CssBaseline />
      {/* content */}
      <Box
        sx={{
          flexGrow: 1,
          marginLeft: constants.CONTENT_SPACING,
          marginRight: constants.CONTENT_SPACING,
          marginTop: "1%",
        }}
      >
        <Stack spacing={1}>
          <Typography data-aos={ANIMATION_STYLE} variant="h2">
            {HEADERS[0]}
          </Typography>
          <Divider
            data-aos={ANIMATION_STYLE}
            style={{
              marginTop: theme.spacing(2),
              marginBottom: theme.spacing(2),
            }}
          />
          <Typography
            variant="body1"
            sx={{ color: theme.palette.main.ashLight }}
            data-aos={ANIMATION_STYLE}
          >
            {data && data.description}
          </Typography>

          <Typography
            variant="h3"
            data-aos={ANIMATION_STYLE}
            style={{ marginTop: theme.spacing(3) }}
          >
            {HEADERS[1]}
          </Typography>

          <Grid container data-aos={ANIMATION_STYLE}>
            {data &&
              data.expertise.map((item, index) => (
                <StyledAvatar alt={item.name} src={item.image} key={index} />
              ))}
          </Grid>

          <Typography
            variant="h3"
            style={{ marginTop: theme.spacing(5) }}
            data-aos={ANIMATION_STYLE}
          >
            {HEADERS[2]}
          </Typography>

          <Grid container data-aos={ANIMATION_STYLE}>
            {data &&
              data.comfortable.map((item, index) => (
                <StyledAvatar alt={item.name} src={item.image} key={index} />
              ))}
          </Grid>

          <Typography
            variant="h3"
            style={{ marginTop: theme.spacing(5) }}
            data-aos={ANIMATION_STYLE}
          >
            {HEADERS[3]}
          </Typography>

          <Grid container data-aos={ANIMATION_STYLE}>
            {data &&
              data.tools.map((tool, index) => (
                <StyledAvatar alt={tool.name} src={tool.image} key={index} />
              ))}
          </Grid>
        </Stack>
      </Box>

      <Box
        sx={{ overflow: "auto", paddingTop: 20 }}
        flexDirection={"column"}
        flexGrow={1}
      />
      {/* footer */}
      <MainFooter />
    </Box>
  );
};

export default SkillsPage;
