import {
  Avatar,
  Chip,
  Grid,
  Hidden,
  Typography,
  useTheme,
} from "@mui/material";
import { styled } from "@mui/material/styles";
import constants from "theme/constants";

const StyledAvatar = styled(Avatar)(({ theme }) => ({
  width: constants.PROJECTS_AVATAR_SIZE,
  height: constants.PROJECTS_AVATAR_SIZE,
  "@media (max-width: 600px)": {
    width: constants.PROJECTS_AVATAR_SIZE_SMALL_SCREEN,
    height: constants.PROJECTS_AVATAR_SIZE_SMALL_SCREEN,
  },
  marginLeft: theme.spacing(0),
  marginRight: theme.spacing(1),
  marginTop: theme.spacing(1),
  "&:not(:first-of-child)": {
    marginLeft: theme.spacing(0),
  },
  "&(:first-of-child)": {
    marginLeft: theme.spacing(0),
  },
  cursor: "pointer",
  transition: "all 0.5s ease-in-out",
  "&:hover": {
    transform: "scale(1.1)",
    transition: "all 0.2s ease-in-out",
  },
}));

const renderImage = (project) => {
  const { ANIMATION_STYLE } = constants;

  return (
    <Grid item xs={12} md={12} sm={12} lg={8} data-aos={ANIMATION_STYLE}>
      <img src={project.image} alt="project" loading="lazy" />
    </Grid>
  );
};

const renderContent = (project, index, theme) => {
  const { title, description, skills, references } = project;
  const { ANIMATION_STYLE } = constants;

  return (
    <Grid
      item
      xs={12}
      md={12}
      sm={12}
      lg={4}
      data-aos={ANIMATION_STYLE}
      sx={{
        textAlign: index % 2 === 0 ? "left" : "right",
        paddingRight: index % 2 === 0 ? theme.spacing(3) : theme.spacing(0),
        paddingLeft: index % 2 === 0 ? theme.spacing(0) : theme.spacing(3),
        display: "flex",
        flexWrap: "wrap",
        justifyContent: "center",
        flexDirection: "column",
        // add breakpoints with theme
        [theme.breakpoints.down("lg")]: {
          textAlign: "left",
          marginLeft: "0",
          paddingLeft: 0,
          marginTop: theme.spacing(2),
        },
      }}
    >
      <Typography
        variant="h3"
        style={{
          [theme.breakpoints.down("lg")]: {
            paddingLeft: theme.spacing(0),
          },
        }}
      >
        {title}
      </Typography>

      <Typography
        variant="body1"
        style={{
          [theme.breakpoints.down("lg")]: {
            paddingLeft: theme.spacing(0),
          },
          color: theme.palette.main.ashLight,
        }}
      >
        {description}
      </Typography>

      {/* skills list */}
      <Grid sx={{ marginTop: theme.spacing(1) }}>
        {skills.map((skill, index) => (
          <Chip
            avatar={<Avatar alt="skills" src={skill.image} />}
            label="Avatar"
            key={index}
            sx={{
              borderRadius: 0,
              marginRight: theme.spacing(1),
              marginTop: theme.spacing(1),
              backgroundColor: skill.color,
              padding: theme.spacing(1),
              color: theme.palette.main.white,
              transition: "all 0.5s ease-in-out",
              "&:hover": {
                transform: "scale(1.05)",
                transition: "all 0.2s ease-in-out",
              },
            }}
          />
        ))}
      </Grid>

      {/* references list */}
      <Grid
        container
        sx={{
          justifyContent: index % 2 === 0 ? "flex-start" : "flex-end",
          marginTop: theme.spacing(1),
          [theme.breakpoints.down("lg")]: {
            justifyContent: "flex-start",
          },
        }}
      >
        {references.map((reference, index) => (
          <StyledAvatar alt="Skill Icon" src={reference.image} key={index} />
        ))}
      </Grid>
    </Grid>
  );
};

const ProjectLeftView = ({ project }) => {
  const theme = useTheme();

  return (
    <Grid container>
      <Hidden lgDown>
        {renderContent(project, 0, theme)}
        {renderImage(project)}
      </Hidden>

      <Hidden lgUp>
        {renderImage(project)}
        {renderContent(project, 0, theme)}
      </Hidden>
    </Grid>
  );
};

const ProjectRightView = ({ project }) => {
  const theme = useTheme();

  return (
    <Grid container>
      {renderImage(project)}
      {renderContent(project, 1, theme)}
    </Grid>
  );
};

const Project = ({ project, index, ...props }) => {
  if (index % 2 === 0) {
    return <ProjectLeftView project={project} />;
  } else {
    return <ProjectRightView project={project} />;
  }
};

export default Project;
