import PropTypes from "prop-types";
import { useLocation } from "react-router-dom";
import { Box, List, ListItemText } from "@mui/material";
import { makeStyles } from "@mui/styles";
import { styled } from "@mui/material/styles";
import StyledLink from "components/StyledLink";

const useStyles = makeStyles((theme) => ({}));

const ListItem = styled(ListItemText)(({ theme }) => ({
  writingMode: "vertical-rl",
  transform: "rotate(180deg)",
  marginBottom: theme.spacing(5),
  cursor: "pointer",
  textTransform: "uppercase",
  display: "block",
  transition: "all 0.3s ease",
  "&:hover": {
    color: theme.palette.main.black,
    transition: "all 0.3s ease",
  },
  fontSize: theme.typography.subtitle2.fontSize,
  color: theme.palette.main.ashLight,
}));

const ActiveListItem = styled(ListItemText)(({ theme }) => ({
  writingMode: "vertical-rl",
  transform: "rotate(180deg)",
  marginBottom: theme.spacing(5),
  cursor: "pointer",
  textTransform: "uppercase",
  display: "block",
  transition: "all 0.3s ease",
  "&:hover": {
    color: theme.palette.main.black,
    transition: "all 0.3s ease",
  },
  fontSize: theme.typography.subtitle2.fontSize,
  color: theme.palette.main.black,
}));

function NavItem({ item, active }) {
  const { title, path, info } = item;

  console.log("active item: " + active);

  return (
    <StyledLink
      to={path}
      sx={{
        textDecoration: "none",
        color: "inherit",
      }}
    >
      {!active && (
        <ListItem disableTypography primary={title}>
          {info && info}
        </ListItem>
      )}

      {active && (
        <ActiveListItem disableTypography primary={title}>
          {info && info}
        </ActiveListItem>
      )}
    </StyledLink>
  );
}

export default function NavSection({ navConfig, ...other }) {
  const { pathname } = useLocation();
  const classes = useStyles();

  return (
    <Box {...other} className={classes.box}>
      <List disablePadding style={{ display: "inline-block" }}>
        {navConfig.map((item) => (
          <NavItem
            key={item.title}
            item={item}
            active={pathname === item.path}
          />
        ))}
      </List>
    </Box>
  );
}

NavSection.propTypes = {
  navConfig: PropTypes.array,
};

NavItem.propTypes = {
  item: PropTypes.object,
  active: PropTypes.bool,
};
