import {
  Box,
  Button,
  CssBaseline,
  Divider,
  Stack,
  Typography,
  useTheme,
} from "@mui/material";
import Aos from "aos";
import "aos/dist/aos.css";
import { MainFooter } from "components/Footer";
import Loading from "components/Loading";
import { child, get, getDatabase, ref } from "firebase/database";
import React, { useEffect, useRef, useState } from "react";
import constants from "theme/constants";
import Project from "./Project";

const HEADERS = ["PROJECTS", "Load More"];

const getData = async () => {
  try {
    const dbRef = ref(getDatabase());
    const snapshot = await get(child(dbRef, `projects`));
    if (snapshot.exists()) {
      return snapshot.val();
    } else {
      console.log("No data available");
    }
  } catch (error) {
    console.log(error);
  }
  return null;
};

const ProjectsPage = () => {
  const theme = useTheme();
  const [projects, setProjects] = useState([]); // only holds the data filtered after pressing load more button
  const [data, setData] = useState(null); // holds the projects and data together  // holds the entire data list
  const [isLoading, setIsLoading] = useState(true);
  var counter = useRef(1);
  const { ANIMATION_STYLE } = constants;
  const INCREMENT_COUNT = 2;

  useEffect(() => {
    Aos.init({ duration: 1000 });
    getData()
      .then((res) => {
        const projectList = [];

        for (let key in res) {
          projectList.push(res[key]);
        }

        console.log(projectList);

        if (res !== null) {
          setData(projectList);
          // fetch first 2 items from projects
          setProjects(projectList.slice(0, INCREMENT_COUNT));
          counter.current = 2;

          setIsLoading(false);
        }
      })
      .catch((error) => {
        console.log(error);
      });
  }, []);

  const onLoadClick = () => {
    setProjects([
      ...projects,
      ...data.slice(counter.current, counter.current + INCREMENT_COUNT),
    ]);
    counter.current = counter.current + INCREMENT_COUNT;
  };

  if (data === null || isLoading) return <Loading />;

  return (
    <Box
      sx={{
        display: "flex",
        flexDirection: "column",
        minHeight: "100vh",
      }}
    >
      <CssBaseline />
      {/* content */}
      <Box
        sx={{
          flexGrow: 1,
          marginLeft: constants.CONTENT_SPACING,
          marginRight: constants.CONTENT_SPACING,
          marginTop: "1%",
        }}
      >
        <Stack spacing={1}>
          <Typography variant="h2" data-aos={ANIMATION_STYLE}>
            {HEADERS[0]}
          </Typography>
          <Divider
            data-aos={ANIMATION_STYLE}
            style={{
              marginTop: theme.spacing(2),
              marginBottom: theme.spacing(2),
            }}
          />

          {projects.map((project, index) => (
            <div
              key={index}
              style={{
                marginTop: index === 0 ? theme.spacing(1) : theme.spacing(8),
              }}
            >
              <Project key={index} project={project} index={index} />
            </div>
          ))}
        </Stack>
      </Box>

      <Box
        sx={{
          marginTop: "10%",
          textAlign: "center",
        }}
      >
        <Button
          variant="contained"
          color="primary"
          onClick={onLoadClick}
          disabled={counter.current >= data.length}
          sx={{
            borderRadius: 0,
            padding: theme.spacing(2),
            marginTop: theme.spacing(4),
            backgroundColor: theme.palette.main.purple,
            boxShadow: "none",
            transition: "all 0.2s ease-in-out",

            // add hover effect
            "&:hover": {
              boxShadow: "none",
              backgroundColor: theme.palette.main.purpleShade,
              // add hover transition
              transition: "all 0.2s ease-in-out",
            },
          }}
        >
          {HEADERS[1]}
        </Button>
      </Box>

      <Box
        sx={{ overflow: "auto", paddingTop: 20 }}
        flexDirection={"column"}
        flexGrow={1}
      />
      {/* footer */}
      <MainFooter />
    </Box>
  );
};

export default ProjectsPage;
