import PropTypes from "prop-types";
import { Link, useLocation } from "react-router-dom";
import { Box, List, ListItemText } from "@mui/material";
import { styled } from "@mui/material/styles";

const ListItem = styled(ListItemText)(({ theme }) => ({
  marginBottom: theme.spacing(6),
  cursor: "pointer",
  textTransform: "uppercase",
  display: "block",
  transition: "all 0.3s ease",
  "&:hover": {
    color: theme.palette.main.black,
    transition: "all 0.3s ease",
  },
  color: theme.palette.main.ashLight,
  fontSize: theme.typography.subtitle2.fontSize,
}));

const ActiveListItem = styled(ListItemText)(({ theme }) => ({
  marginBottom: theme.spacing(6),
  cursor: "pointer",
  textTransform: "uppercase",
  display: "block",
  transition: "all 0.3s ease",
  "&:hover": {
    color: theme.palette.main.black,
    transition: "all 0.3s ease",
  },
  color: theme.palette.main.black,
  fontSize: theme.typography.subtitle2.fontSize,
}));

const StyledLink = styled(Link)`
  text-decoration: none;

  &:focus,
  &:hover,
  &:visited,
  &:link,
  &:active {
    text-decoration: none;
  }
`;

function NavItem({ item, active, setMenuOpen }) {
  const { title, path, info } = item;

  return (
    <StyledLink
      to={path}
      sx={{
        textDecoration: "none",
        color: "inherit",
      }}
      onClick={() => setMenuOpen(false)}
    >
      {!active && (
        <ListItem disableTypography primary={title}>
          {info && info}
        </ListItem>
      )}

      {active && (
        <ActiveListItem disableTypography primary={title}>
          {info && info}
        </ActiveListItem>
      )}
    </StyledLink>
  );
}

export default function NavSectionMobile({ navConfig, setMenuOpen, ...other }) {
  const { pathname } = useLocation();

  return (
    <Box {...other}>
      <List disablePadding style={{ display: "inline-block" }}>
        {navConfig.map((item) => (
          <NavItem
            key={item.title}
            item={item}
            active={pathname === item.path}
            setMenuOpen={setMenuOpen}
          />
        ))}
      </List>
    </Box>
  );
}

NavItem.propTypes = {
  item: PropTypes.object,
  active: PropTypes.bool,
};

NavSectionMobile.propTypes = {
  navConfig: PropTypes.array,
};
