import { Box, Stack, IconButton, Typography, Container } from "@mui/material";
import { useTheme } from "@mui/material/styles";
import FacebookIcon from "@mui/icons-material/Facebook";
import GitHubIcon from "@mui/icons-material/GitHub";
import LinkedInIcon from "@mui/icons-material/LinkedIn";

const MobileFooter = () => {
  const theme = useTheme();

  var social = localStorage.getItem("socialMedia");
  if (social) {
    social = JSON.parse(social);
    console.log(social);
  }

  return (
    <Box
      component="footer"
      sx={{
        py: 3,
        px: 2,
        mt: "auto",
        backgroundColor: (theme) =>
          theme.palette.mode === "light"
            ? theme.palette.grey[200]
            : theme.palette.grey[800],
      }}
    >
      <Container
        sx={{
          flexGrow: 1,
          display: "flex",
          alignItems: "left",
        }}
      >
        <Stack
          direction="column"
          alignItems="left"
          spacing={{ xs: 0.5, sm: 1.5 }}
          sx={{ flexGrow: 1, textAlign: "left" }}
        >
          <Typography
            variant="subtitle2"
            fontWeight={theme.typography.fontWeightBold}
            color="inherit"
            noWrap
          >
            Email
          </Typography>
          <Typography variant="subtitle2" color="inherit" noWrap>
            {social && social.email}
          </Typography>
        </Stack>

        <Stack
          direction="row"
          alignItems="center"
          spacing={{ xs: 0.5, sm: 1.5 }}
        >
          <IconButton
            size="large"
            color={"primary"}
            target="_blank"
            href={social && social.facebook}
          >
            <FacebookIcon color="secondary" />
          </IconButton>

          <IconButton size="large" target="_blank" href={social && social.github}>
            <GitHubIcon
              color="primary"
              sx={{ color: theme.palette.main.black }}
            />
          </IconButton>

          <IconButton
            size="large"
            color={"primary"}
            target="_blank"
            href={social && social.linkedin}
          >
            <LinkedInIcon
              color="secondary"
              sx={{ color: theme.palette.main.blue }}
            />
          </IconButton>
        </Stack>
      </Container>
    </Box>
  );
};
export default MobileFooter;
