import * as React from "react";
import Tabs from "@mui/material/Tabs";
import Tab from "@mui/material/Tab";
import Typography from "@mui/material/Typography";
import Box from "@mui/material/Box";
import styled from "@emotion/styled";
import { useTheme } from "@emotion/react";
import { Stack } from "@mui/material";
import constants from "theme/constants";

// {
//   institution: "University of California",
//   program: "Computer Science",
//   cgpa: "3.48",
//   startDate: "Aug 2017",
//   endDate: "May 2021",
// },

const TAB_WIDTH = 35;

function TabPanelEducation(props) {
  const { children, value, index, ...other } = props;

  const theme = useTheme();

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`vertical-tabpanel-${index}`}
      aria-labelledby={`vertical-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Stack sx={{ pl: (theme) => theme.spacing(8) }} spacing={1}>
          <Typography variant="subtitle1">{children.institution} </Typography>
          <Typography variant="body2" sx={{ color: theme.palette.main.purple }}>
            {children.program}
          </Typography>
          <Typography variant="body2">CGPA {children.cgpa}</Typography>
          <Typography variant="body2">
            {children.startDate} to {children.startDate}
          </Typography>
        </Stack>
      )}
    </div>
  );
}

// company: "Pioneer Alpha Ltd",
// title: "Junior Software Engineer",
// startDate: "Aug 2017",
// endDate: "May 2021",
// experience: [
//   "Developed a web application for managing the company's inventory",
//   "Enthusiast with an immense interest in We , UI/UX Design, and Cloud Computing.",
//   "A passionate individual who loves spending time on fixing little details and creating optimized solutions.",
// ],

function TabPanelWork({ children, value, index, ...other }) {
  const theme = useTheme();
  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`vertical-tabpanel-${index}`}
      aria-labelledby={`vertical-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Stack sx={{ pl: (theme) => theme.spacing(8) }} spacing={1}>
          <Typography variant="subtitle1">{children.company} </Typography>
          <Typography variant="body2" sx={{ color: theme.palette.main.purple }}>
            {children.title}
          </Typography>
          <Typography variant="body2">
            {children.startDate} to {children.startDate}
          </Typography>
        </Stack>
      )}
    </div>
  );
}

function a11yProps(index) {
  return {
    id: `vertical-tab-${index}`,
    "aria-controls": `vertical-tabpanel-${index}`,
  };
}

// create a styled Tab component
const StyledTab = styled(Tab)(({ theme }) => ({
  ...theme.typography.subtitle1,
  WebkitAlignItems: "left",
  paddingLeft: 0,
  paddingTop: 0,
  paddingRight: theme.spacing(5),
  alignItems: "flex-start",
  textAlign: "left",
}));

const Education = ({ content }) => {
  const [value, setValue] = React.useState(0);
  const { ANIMATION_STYLE } = constants;

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  return (
    <Box
      sx={{
        flexGrow: 1,
        bgcolor: (theme) => theme.palette.main.background,
        display: "flex",
        paddingTop: 1,
      }}
      data-aos={ANIMATION_STYLE}
    >
      <Tabs
        orientation="vertical"
        // variant="scrollable"
        value={value}
        onChange={handleChange}
        aria-label="Vertical tabs example"
        sx={{
          borderRight: 1,
          borderColor: "divider",
          minWidth: (theme) => theme.spacing(TAB_WIDTH),
          maxWidth: (theme) => theme.spacing(TAB_WIDTH),
        }}
      >
        {content.map((item, index) => {
          const { type } = item;
          return <StyledTab label={type} {...a11yProps(index)} key={index} />;
        })}
      </Tabs>
      {content.map((item, index) => {
        return (
          <TabPanelEducation value={value} index={index} key={index}>
            {item}
          </TabPanelEducation>
        );
      })}
    </Box>
  );
};

const WorkExperience = ({ content }) => {
  const [value, setValue] = React.useState(0);
  const { ANIMATION_STYLE } = constants;

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  return (
    <Box
      data-aos={ANIMATION_STYLE}
      sx={{
        flexGrow: 1,
        bgcolor: (theme) => theme.palette.main.background,
        display: "flex",
        paddingTop: 1,
      }}
    >
      <Tabs
        orientation="vertical"
        value={value}
        onChange={handleChange}
        aria-label="Vertical tabs example"
        sx={{
          borderRight: 1,
          borderColor: "divider",
          minWidth: (theme) => theme.spacing(TAB_WIDTH),
          maxWidth: (theme) => theme.spacing(TAB_WIDTH),
        }}
      >
        {content.map((item, index) => {
          const { title } = item;
          return <StyledTab label={title} {...a11yProps(index)} key={index} />;
        })}
      </Tabs>
      {content.map((item, index) => {
        return (
          <TabPanelWork value={value} index={index} key={index}>
            {item}
          </TabPanelWork>
        );
      })}
    </Box>
  );
};

export default function TabLayout({ content, type, ...props }) {
  if (type === "education") {
    return <Education content={content} />;
  } else {
    return <WorkExperience content={content} />;
  }
}

