import {
  Box,
  CssBaseline,
  Divider,
  Hidden,
  Stack,
  Typography,
  useTheme,
} from "@mui/material";
import { MainFooter } from "components/Footer";
import constants from "theme/constants";
import TabLayout from "./TabLayout";
import TabLayoutMobile from "./TabLayoutMobile";
import Aos from "aos";
import "aos/dist/aos.css";
import React from "react";
import Loading from "components/Loading";
import { child, get, getDatabase, ref } from "firebase/database";

const HEADERS = {
  title: "About me",
  education: "Education",
  workExperience: "Work Experience",
};

const getData = async () => {
  try {
    const dbRef = ref(getDatabase());
    const snapshot = await get(child(dbRef, `about`));
    if (snapshot.exists()) {
      return snapshot.val();
    } else {
      console.log("No data available");
    }
  } catch (error) {
    console.log(error);
  }
  return null;
};

const AboutPage = () => {
  const theme = useTheme();
  const { ANIMATION_STYLE } = constants;
  const [data, setData] = React.useState(null);
  const [loading, setLoading] = React.useState(true);

  React.useEffect(() => {
    Aos.init({ duration: 1000 });
    getData()
      .then((data) => {
        setData(data);
        setLoading(false);
      })
      .catch((error) => {
        console.log(error);
      });
  }, []);

  if (data === null || loading) return <Loading />;

  return (
    <Box
      sx={{
        display: "flex",
        flexDirection: "column",
        minHeight: "100vh",
      }}
    >
      <CssBaseline />
      {/* content */}
      <Box
        sx={{
          flexGrow: 1,
          marginLeft: constants.CONTENT_SPACING,
          marginRight: constants.CONTENT_SPACING,
          marginTop: "1%",
        }}
      >
        {/* left side content */}
        <Stack spacing={1}>
          <Typography
            variant="h2"
            sx={{ textTransform: "capitalize" }}
            data-aos={ANIMATION_STYLE}
          >
            {HEADERS.title}
          </Typography>
          <Divider
            data-aos={ANIMATION_STYLE}
            style={{
              marginTop: theme.spacing(2),
              marginBottom: theme.spacing(2),
            }}
          />
          <Typography
            variant="body1"
            data-aos={ANIMATION_STYLE}
            sx={{ color: theme.palette.main.ashLight }}
          >
            {data && data.description}
          </Typography>

          <Typography
            variant="h3"
            style={{ marginTop: theme.spacing(5) }}
            data-aos={ANIMATION_STYLE}
          >
            {HEADERS.education}
          </Typography>

          {/* education */}
          <Hidden mdDown>
            <TabLayout
              content={data && data.education}
              type={"education"}
              animation={ANIMATION_STYLE}
            />
          </Hidden>

          <Hidden mdUp>
            <TabLayoutMobile
              content={data && data.education}
              type={"education"}
              animation={ANIMATION_STYLE}
            />
          </Hidden>

          <Typography
            variant="h3"
            style={{ marginTop: theme.spacing(7) }}
            data-aos={ANIMATION_STYLE}
          >
            {HEADERS.workExperience}
          </Typography>

          {/* work experience */}
          <Hidden mdDown>
            <TabLayout
              content={data && data.workExperience}
              type={"workExperience"}
              animation={ANIMATION_STYLE}
            />
          </Hidden>

          <Hidden mdUp>
            <TabLayoutMobile
              content={data && data.workExperience}
              type={"workExperience"}
              animation={ANIMATION_STYLE}
            />
          </Hidden>
        </Stack>
      </Box>

      <Box
        sx={{ overflow: "auto", paddingTop: 20 }}
        flexDirection={"column"}
        flexGrow={1}
      />
      {/* footer */}
      <MainFooter />
    </Box>
  );
};

export default AboutPage;
